*{
    box-sizing: border-box;
}



.main_item{
    margin-bottom: 50px;

    display: flex;
    justify-content: space-between;
    align-items: center;
    height: auto;
    min-height: 200px;
    border-bottom: 1px solid #222;
    border-top: 1px solid #222;
}

.item_img{
    display: flex;
    align-items: center;
    color: #000;
    

}

.item_opis{
    margin-left: 50px;
}

.item_opis h2{
    margin-bottom: 10px;
    font-size: 24px;
    font-weight: 500;
}

.item_opis p{
    margin-bottom: 5px;
}

.item_opis img{
    margin-top: 23px;
}

.main_shop h3{
    margin-bottom: 77px;
    font-weight: 500;
    font-size: 24px;
}

.item_btn a{
    padding: 7px 12px;
    background-color: #2142B6;
    color: #fff;
    transition: all ease .3s;
}

.item_btn a:hover{
    background-color: #162e84 ;
}

.main_img{
    animation: main ease .4s;
}

@keyframes main {
    0%{
        transform: scale(0);
    }

    100%{
        transform: scale(1);
    }
}


@media (max-width: 745px) {
    .main_item{
        flex-direction: column;
        justify-content: space-evenly;
        min-height: 350px;
    }

    .main_shop h3{
        margin-bottom: 20px;
    }
    
}



@media (max-width: 480px) {
    .item_img{
        flex-direction: column;

    }

    .main_item{
        min-height: 450px;
    }

    .item_opis{
        text-align: center;
        margin-left: 0;
        margin-top: 20px;
    }
    
}