nav {
    height: 80px;
    z-index: 1;
    top: 0;
    width: 100%;
    position: fixed;
    background-color: rgba(34, 34, 34, 0.85);
    backdrop-filter: blur(5px);
}

.nav_c {

    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

nav h2 {
    color: #fff;
    font-family: 'Merriweather', serif;
    font-weight: bold;
    font-size: 24px;
}

.nav_btns {
    display: flex;
    align-items: center;
}

.nav_btns a {
    color: #fff;
    margin-left: 50px;
    font-weight: 300;
    font-size: 16px;
}

.act::after {
    display: block;
    content: '';
    width: 100%;
    height: 1px;
    margin-top: 5px;
    background-color: #fff;
    opacity: 0;
    transition: all ease-in-out .5s;
}

.act:hover::after {
    opacity: 1;
}

.bas{
    margin-left: 50px;
}

.log{
    cursor: pointer;
    margin-left: 25px;
}

.window_log{
    width: 530px;
    height: 530px;
    background-color: #171616;
    position: fixed;
    left: 50%;
    transform: translate(-50%, 5%);
    border-radius: 10px;
    animation: a_windows ease-in .3s;
    z-index: 1111111;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

@keyframes a_windows {
    0%{
        left: 100%;
        opacity: 0;
    }

    100%{
        left: 50%;
        opacity: 1;
    }
}

.one_btn {
    padding: 7px 12px;
    background-color: #2142B6;
    color: #fff;
    transition: all ease .3s;

}
.one_btn:hover {
    background-color: #162e84;
}



.close img{
    padding: 25px;
}

.window_log h2{
    color: #fff;
    font-size: 32px;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
}

.window_inp input{
    width: 93%;
    height: 35px;
    margin-bottom: 20px;
    border-radius: 5px;
    border: 0;
    padding-left: 10px;
}

.window_inp{
    margin-bottom: 20px;
}

.window_inp{
    text-align: center;
    margin-top: 60px;
}

.window_log h3{
    color: #fff;
    margin-bottom: 20px;
    font-size: 24px;
    margin-left: 25px;
    font-weight: 500;
}


.window_btn a{
    margin-left: 18px;
}

@media (max-width: 930px) {
    .nav_btns a{
        margin-left: 35px;
    }
}

@media (max-width: 805px) {
    .nav_btns {
        display: none;
    }
}