
.b_item{
    margin-top: 50px;
    height: auto;
    min-height: 200px;
    width: 100%;
    border-top: 1px solid #222222;
    border-bottom: 1px solid #222222;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    flex-wrap: wrap;
}

.count{
    display: flex;
    align-items: center;
}

.count img{
    margin: 0 5px;
    cursor: pointer;
}


.item_right{
    display: flex;
    align-items: center;

}

.dr{
    margin-right: 50px;
}

.item_right h2{
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 24px;
}


.b_item p{
    color: #222;
    font-size: 16px;
    margin-bottom: 5px;
}

.del{
    margin-top: 10px;
    cursor: pointer;
}

.item_left h3{
    font-weight: 500;
    font-size: 24px;
    margin-top: 10px;
}

.sum{
    margin-top: 15px;
}


@media (max-width: 684px) {
    .b_item{
        justify-content: right;
    }

    .item_right{
        margin-bottom: 50px;
    }
}
 

@media (max-width: 456px) {
    .item_right{
        flex-wrap: wrap;
        justify-content: center;
    }

    .dr{
        margin-right: 0;
    }

    .b_item{
        justify-content: center;
    }
}