.tovar {
    min-height: 100vh;
    padding-top: 150px;
}

.t_main {
    
    display: flex;
    justify-content: space-between;
}

.t_imgs {
    width: 50%;
    text-align: center;
    
}

.img_main {
   height: 260px;

}

.img_items {
    display: flex;
    align-items: center;
    justify-content: center;
}

.img_items img {
    width: 100px;
    margin-left: 20px;
    cursor: pointer;
    transition: all ease .3s;
}

.img_items img:hover {
    transform: scale(1.1);
}

.img_main{
    animation: main ease .4s;
}

@keyframes main {
    0%{
        transform: scale(0)
    }

    100%{
        transform: scale(1);
    }
}


.t_opis h2{
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 20px;
}

.t_opis p{
    color: #222;
    margin-bottom: 20px;
    width: 100%;
    max-width: 540px;
}

.t_opis h3{
    font-size: 18px;
    font-weight: 300;
    margin-bottom: 5px;
}

.one_btn{
    padding: 7px 12px;
    background-color: #2142B6;
    color: #fff;
    margin-right: 20px;
}

.two_btn{
    padding: 7px 40px;
    background-color: #2142B6;
    color: #fff;
}

.opis_btn{
    margin-top: 80px;
    margin-bottom: 50px;
}

.opis_btn a{
    transition: all ease .3s;
}

.opis_btn a:hover{
    background-color: #162e84;
}


@media (max-width: 1230px) {
    .t_main{
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .t_main h3{
        text-align: center;
    }

    
    .img_main{
        width: 100%;
    }

    .t_imgs{
        width: 100%;
    }

    
}


@media (max-width: 450px) {
   

    .img_items img{
        margin-left: 0;
    }

  

    .img_items{
        margin-top: 20px;
        margin-bottom: 40px;
    }
}