.block_item{
    width: 100%;
    min-height: 200px;
    height: auto;
    background-color: #110E0D;
    margin-top: 20px;
    margin-bottom: 20px;
}

.head_item{
    height: auto;
    min-height: 50px;
    width: 100%;
    flex-wrap: wrap;
    display: flex;
    justify-content: space-between;
    align-items: center;
}




.head_item p{
    padding: 5px;
    color: #2142B6;
    margin:  0 15px;
    font-size: 16px;
}

.head_item span{
    color: #fff;
}

.main_item{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.item_text{
    margin-left: 15px;
}

.item_text h3{
    color: #2142B6;
    font-size: 16px;

}

.item_text p{
    color: #fff;
    font-size: 16px;

}

.last_text{
    margin-right: 15px;
    color: #2142B6;
}

.last_text span{
    color: #fff;
}

.item_btn{
    align-self: flex-end;
}

.item_btn img{
    margin-left: 20px;
    cursor: pointer;
} 

@media (max-width: 743px) {
    .block_item{
        padding: 10px;

    }
}
@media (max-width: 476px) {
    .main_item{
        flex-wrap: wrap;
    }

    .last_text{
        margin-left: 15px;
    }
}

