.not_b {
    margin: 200px auto;
    /* display: none; */
    text-align: center;
    min-height: 50vh;
    height: auto;
}

.yep_b {
    margin: 200px auto;
    min-height: 50vh;
    height: auto;


}


.not_b img {
    margin-bottom: 30px;
}

.not_b h2 {
    margin-bottom: 15px;
    font-weight: 500;
    font-size: 36px;

}

.yep_h2 {
    text-align: right;
    margin-top: 50px;
    font-size: 36px;
    font-weight: 400;
}

.one_btn {
    padding: 7px 12px;
    background-color: #2142B6;
    color: #fff;
    transition: all ease .3s;
}

.block_yep {
    text-align: right;
    margin-top: 20px;
}

.one_btn:hover {
    background-color: #162e84;
}

.model_window {
    height: 600px;
    width: 530px;
    background-color: #171616;
    position: fixed;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.close img{
    padding: 25px;
}

.model_window h2{
    color: #fff;
    font-size: 32px;
    text-align: center;
}

.window_inp input{
    width: 93%;
    height: 35px;
    margin-bottom: 20px;
    border-radius: 5px;
    border: 0;
    padding-left: 10px;
}

.window_inp{
    text-align: center;
    margin-top: 60px;
}

.model_window h3{
    color: #fff;
    margin-bottom: 20px;
    font-size: 24px;
    margin-left: 25px;
    font-weight: 500;
}


.window_btn a{
    margin-left: 25px;
}

.model_window{
    border-radius: 10px;
    animation: a_windows ease-in .3s;
    z-index: 1111111;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}


@keyframes a_windows {
    0%{
        left: 100%;
        opacity: 0;
    }

    100%{
        left: 50%;
        opacity: 1;
    }
}

@media (max-width: 576px) {
   .model_window{
    height: 600px;
    max-width: 530px;
    width: 95%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
   }
}