.hamburger-menu{
    display: none;
    position: fixed;
    z-index: 11;
}


#menu__toggle {
    opacity: 0;
  }
  #menu__toggle:checked + .menu__btn > span {
    transform: rotate(45deg);
  }
  #menu__toggle:checked + .menu__btn > span::before {
    top: 0;
    transform: rotate(0deg);
  }
  #menu__toggle:checked + .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
  }
  #menu__toggle:checked ~ .menu__box {
    right: 0 !important;
  }
  .menu__btn {
    position: fixed;
    top: 40px;
    z-index: 1111;
    right: 20px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
  }
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
    transition-duration: .25s;
    
  }
  .menu__btn > span::before {
    content: '';
    top: -8px;
  }
  .menu__btn > span::after {
    content: '';
    top: 8px;
  }
  .menu__box {
    display: block;
    position: fixed;
    top: 80px;
    right: -100%;
    width: 50%;
    height: 100vh;
    margin: 0;
    padding: 20px 0;
    list-style: none;
    background-color: rgba(34, 34, 34, 0.85);
    backdrop-filter: blur(5px);
    box-shadow: 2px 2px 6px rgba(0, 0, 0, .4);
    transition-duration: .25s;
  }
  .menu__item {
    display: block;
    padding: 12px 24px;
    color: #fff;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 600;
    text-decoration: none;
    transition-duration: .25s;

  }
  .menu__item:hover {
    background-color: rgb(29, 29, 29);
  }

  .menu_imgs{
    text-align: center;
    margin-top: 15px;
  }

  .img{
    margin-right: 20px;
  }

  .log{
    cursor: pointer;
    margin-left: 25px;
}

.window_log{
    max-width: 530px;
    width: 95%;
    height: 530px;
    background-color: #171616;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    animation: a_windows ease-in .3s;
    z-index: 1111111;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

@keyframes a_windows {
    0%{
        left: 100%;
        opacity: 0;
    }

    100%{
        left: 50%;
        opacity: 1;
    }
}

.one_btn {
    padding: 7px 12px;
    background-color: #2142B6;
    color: #fff;
    margin-left: 20px;
    transition: all ease .3s;

}
.one_btn:hover {
    background-color: #162e84;
}



.close img{
    padding: 25px;
}

.window_log h2{
    color: #fff;
    font-size: 32px;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
}

.window_inp input{
    width: 93%;
    height: 35px;
    margin-bottom: 20px;
    border-radius: 5px;
    border: 0;
    padding-left: 10px;
}

.window_inp{
    margin-bottom: 20px;
}

.window_inp{
    text-align: center;
    margin-top: 60px;
}

.window_log h3{
    color: #fff;
    margin-bottom: 20px;
    font-size: 24px;
    margin-left: 25px;
    font-weight: 500;
}

  @media (max-width: 805px) {
    .hamburger-menu{
        display: block;
    }
  }


 
  